.card {
    width: 100%;
    height: 700px;
   background-color: gold;
    padding: 20px 10px;
    margin-bottom: 10px;
    overflow: hidden;
  
}

.card-info {
    display: flex;
    align-items: center;
    margin-left: 40px;
    
}

.card-info img {
    border-radius: 50%;
    height: 60px;
    width: 60px;
    border: 1px solid grey;
}

.card-info span a {
  font-size: 14px;
  text-decoration: none;
  color: blue;
}

.card-info span div {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 20px;
  }
  
.card-info span a:hover {
  
  text-decoration: underline;
}

.card-info span {
    margin-left: 10px;
}

.post {
 margin-top: 30px;
 padding: 0 10px;
 display: flex;
 flex-direction: column;
 align-items: center;
 overflow-y: hidden;
}



.post h1 {
    text-align: center;
    
    text-transform: capitalize;
    font-family: Arial, Helvetica, sans-serif;
}

.post p {
    text-indent: 50px;
    font-size: 19px;
    height: 270px;
    word-spacing: 3px;
    line-height: 1.7;
    margin: 30px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 480;
}

.buttons {
    display: flex;
    justify-content: space-evenly;

}

.buttons button {
    height: 45px;
    width: 100px;
    border-radius: 10px;
    font-size: 16px;
    background: #eee;
    background-blend-mode: saturation;
    border: 1px solid lightgray;
}